<template>
  <div class="index">
    <div class="left-panel">
      <div id="logo">
        <span id="logo-item">DRGraph</span>
      </div>
      <data-set ref="dset" :runBtn="runBtn" @runGraphLayout="runGraphLayout"></data-set>
      <parameters ref="params"></parameters>
      <encoding ref="encod"></encoding>
      <div class="copyright">
        <span>VIS 2018</span>
      </div>
    </div>
    <div class="graph-container">
      <img id="image" :src="imgpath" alt="">
    </div>
    <div class="runningtime">
      <span>Running time:  {{ runningtime.toFixed(3) }}s</span>
      <span>Rendering time:  {{ renderingtime.toFixed(3) }}s</span>
    </div>
  </div>
</template>

<script>
import config from '../utils/config'
export default {
  name: 'Index',
  data () {
    return {
      imgpath: '',
      runBtn: false,
      runningtime: 0.000,
      renderingtime: 0.000
    }
  },
  methods: {
    runGraphLayout () {
      let dset = this.$refs.dset
      let params = this.$refs.params
      // let encod = this.$refs.encod
      this.runBtn = true
      // console.log(dset)
      // console.log(params)
      // console.log(encod)
      this.imgpath = ''
      this.runningtime = 0.000
      this.renderingtime = 0.000
      this.axios({
        method: 'post',
        url: config.url + '/api/runGraphLayout/',
        data: {
          input: dset.choosedDataset,
          dismax: params.dismax,
          sample: params.n_sample,
          gamma: params.gamma,
          negative: params.negative
          // smaplingRate: encod.samplingRate
        }
      }).then((response) => {
        let result = response.data
        if (result.status === 'success') {
          this.imgpath = config.url + '/static/' + result.png_path
          this.runningtime = parseFloat(result['runningtime'])
          this.renderingtime = parseFloat(result['renderingtime'])
        }
      }).catch(function (error) {
        console.log(error)
      }).finally(() => {
        this.runBtn = false
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.index {
  width: 100%;
  height: 100%;
}
.left-panel {
  width: 330px;
  height: 1080px;
  float: left;
  background: #F3F3F3;
  box-shadow: 2px 0 5px 0 rgba(0,0,0,0.50);
}
.graph-container {
  margin-left: 330px;
  overflow: hidden;
  height: 1080px;
  text-align: center;
}
#logo{
  height: 127px;
  background-color: #F3F3F3;
  text-align: center;
}
#logo-item {
  /* margin-left: 85px; */
  line-height: 127px;
  font-weight: bold;
  font-size: 36px;
  color: #000000;
  text-align: center;
}
.copyright {
  font-size: 14px;
  color: #666666;
  line-height: 100px;
  text-align: center;
}

/* common style */
.title {
  width: 330px;
  height: 45px;
  font-size: 15px;
  color: #333333;
  background: #E2E2E2;
  line-height: 45px;
  font-weight: bold;
}
.slider-name {
    width: 190px;
    font-weight: bold;
    font-size: 14px;
    color: #666666;
    float: left;
    line-height: 35px;
}
.slider {
    margin-left: 5px;
    float: left;
    width: 50px;
}
.ivu-slider-button:hover {
  border-color: #979797;
  transform: scale(1.3);
}
.ivu-slider-button {
  border:1px solid #979797;
  background-color: #D8D8D8;
}
.ivu-input-number {
  width: 50px;
}
.ivu-slider-input .ivu-slider-wrap {
  width: 100px;
  margin-right: 0;
}
#image {
  height: 100%;
}
.runningtime {
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 18px;
  width: 220px;
}
.runningtime span {
  float: left;
}
</style>



