<template>
    <div class="parameters">
        <div class="title">
            <span style="margin-left:15px">Parameters</span>
        </div>
        <div class="content">
            <div class="n_sample">
                <span class="slider-name">Iteration number</span>
                <div class="slider">
                    <Input v-model="n_sample" number></Input>
                </div>
                <div style="clear:both"></div>
            </div>
            <div class="dismax">
                <span class="slider-name"><i>K</i>-order nearest neighbors</span>
                <div class="slider">
                    <Input v-model="dismax" number ></Input>
                </div>
                <div style="clear:both"></div>
            </div>
             <div class="gamma">
                <span class="slider-name">Gamma</span>
                <div class="slider">
                    <Input v-model="gamma" number></Input>
                </div>
                <div style="clear:both"></div>
            </div>
             <div class="negative">
                <span class="slider-name">Negative sample number</span>
                <div class="slider">
                    <Input v-model="negative" number></Input>
                </div>
                <div style="clear:both"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'parameters',
  data () {
    return {
      dismax: 1,
      n_sample: 500,
      gamma: 1.0,
      negative: 10
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.parameters {
    height: 299px;
    width: 330px;
    background-color: #F9F9F9;
}
.n_sample, .dismax, .gamma, .negative{
    width: 280px;
    margin-left: 35px;
    height: 35px;
}
.n_sample{
    margin-top: 20px;
}
</style>



