<template>
    <div class="data-set">
        <div class="title">
            <span style="margin-left:15px">Dataset</span>
        </div>
        <div class="content">
            <div class="show-info">
                <div class="data-title">Choose Dataset</div>
                <input id="chodata" type="text" v-model="choosedDataset" readonly='readonly'>
                <div>
                    <button id="run" @click="runGraphLayout" :disabled="runBtn">{{ runTitle }}</button>
                </div>
            </div>
            <div class="file-list">
                <div class="upload row" @click="selectFile">
                    <input type="file" id="upload_file" style="display:none" @change="uploadFile" accept=".txt">
                    <div style="width:22px; float:left">
                        <Icon type="folder" color="#EDD574" size="20"></Icon>
                    </div>
                    <span>import from file</span>
                </div>
                <span>import from datasets</span>
                <div class="datasets">
                    <div class="wrap">
                        <div  v-for="item in datasets" :key="item.id"
                          :class="{ selected: item === choosedDataset, item: item !== choosedDataset}" @click="showDataset(item)">
                            {{ item }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import config from '../utils/config'
export default {
  name: 'data-set',
  props: {
    runBtn: Boolean
  },
  data () {
    return {
      choosedDataset: 'dwt_1005',
      datasets: ['dwt_1005', 'dwt_419', '3elt', 'fe_ocean'],
      runTitle: 'start'
    }
  },
  watch: {
    runBtn (val) {
      this.runTitle = (!val) ? 'start' : 'running'
    }
  },
  created () {
    this.getDataset()
  },
  methods: {
    getDataset (dataname) {
      this.axios({
        method: 'get',
        url: config.url + '/api/getDatasets/'
      }).then((response) => {
        this.datasets = response.data
        if (dataname) {
          this.showDataset(dataname)
        } else {
          this.showDataset(this.datasets[0])
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    runGraphLayout () {
      this.$emit('runGraphLayout')
    },
    showDataset (data) {
      this.choosedDataset = data
    },
    selectFile () {
      document.getElementById('upload_file').click()
    },
    uploadFile (d) {
      console.log(d.target.files)
      let form = new FormData()
      form.append('file', d.target.files[0])
      this.axios({
        method: 'post',
        url: config.url + '/api/upload/',
        data: form
      }).then((response) => {
        if (response.data['status'] === 0) {
          this.getDataset(response.data['data'])
          this.$Message.success(response.data['data'] + '.txt upload complete')
        } else if (response.data['status'] === 1) {
          this.$Message.warning(response.data['data'] + '.txt already exists')
        } else if (response.data['status'] === 3) {
          this.$Message.error('Error! for more details, please see console!')
          console.log(response.data['error'])
        }
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.data-set {
    height: 255px;
    width: 330px;
    background-color: #F9F9F9;
}
.show-info {
    width: 165px;
    height: 210px;
    float: left;
    padding-left: 51px;
    padding-top: 22px;
    font-size: 14px;
    color: #666666;
}
.data-title {
    font-weight: bold;
}
#chodata {
    width: 70px;
    border: 0px;
    font-style: italic;
    border-bottom: 1px solid #979797;
    background-color: #F9F9F9 ;
}
#run {
    margin-top: 21px;
    width: 59px;
    height: 27px;
    background-color: #FFFFFF;
    border: 1px solid #979797;
    border-radius: 3px;
}
.file-list {
    width: 165px;
    height: 210px;
    float: left;
    padding-top: 22px;
    padding-left: 20px;
    font-size: 11px;
    color: #666666;
}
.row {
    height: 21px;
    margin-bottom: 2px;
}
.row span {
    height: 21px;
    line-height: 21px;
}
.datasets {
    margin-top: 11px;
    width: 89px;
    height: 90px;
    border: 1px dashed #979797;
    padding: 6px;
}
.wrap{
    width: 100%;
    height: 100%;
    /* overflow:auto; */
    overflow-y: scroll;
    overflow-x: hidden;
    white-space: nowrap;
    /* -o-text-overflow:ellipsis; */
    text-overflow: ellipsis;
}
.wrap div {
  overflow: hidden;
}
.wrap::-webkit-scrollbar {
    width: 2px;
}
.wrap::-webkit-scrollbar-thumb {
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 5px#BEBEBE ;
    background:#BEBEBE;
}
.item {
  margin-right: 7px;
}
.item:hover {
  transform: scale(1.2);
  margin:0 7px;
}
.selected {
  background-color: #D8D8D8;
  margin-right: 7px;
}
.upload {
    width: 125px;
    position: relative;
    left: -10px;
    padding-left: 10px;
}
.upload:hover {
    border: 1px solid #5cadff;
    border-radius: 3px;
}
</style>



